.stopwatch-board {
  margin: 15px auto;
  justify-content: center;
  align-items: center;
}
.wrap-watch {
  position: relative;
}
.reset-time-btn {
  position: absolute;
  right: 26%;
  top: -18%;
  opacity: 0;
  visibility: hidden;
  transition: 0.2s;
}
.reset-time-btn button {
  border: none;
  border-radius: 50%;
  background-color: #ffc107;
  font-size: 20px;
  line-height: 0.4;
  width: 30px;
  height: 30px;
}
.wrap-watch:hover .reset-time-btn {
  opacity: 1;
  visibility: visible;
}

/* reset time button responsive */
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .reset-time-btn {
    right: 22%;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .reset-time-btn {
    right: 14%;
  }
}
@media only screen and (max-width: 767px) {
  .reset-time-btn {
    right: 14%;
  }
}
